.moneyListItem {
    display: flex;
    align-items: center;
    padding: 5px;
    border-radius: 5px;
    &__number{
        font-size: 18px;
        font-weight: 100;
        width: 30%;
    }
    &__amount{
        font-size: 20px;
        font-weight: 300;
    }
}
.moneyListItem.active{
    background-color: teal;
}

.save {
    color: gold;
}